@import url("https://fonts.googleapis.com/css2?family=Inter:wght@200;400;600;700&display=swap");
:root {
  --lime-green: hsl(163, 72%, 41%);
  --bright-red: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram: linear gradient hsl(37, 97%, 70%) to hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-dark: linear gradient hsl(210, 78%, 56%) to hsl(146, 68%, 55%);
  --very-dark-blue-background: hsl(230, 17%, 14%);
  --very-dark-blue-background-pattern: hsl(232, 19%, 15%);
  --dark-desaturated-blue-card: hsl(228, 28%, 20%);
  --desaturated-blue-card: hsl(228, 34%, 66%);
  --white-text: hsl(0, 0%, 100%);
  --toggle-light: hsl(230, 22%, 74%);
  --white-background: hsl(0, 0%, 100%);
  --very-pale-blue-background-pattern: hsl(225, 100%, 98%);
  --light-grayish-blue-card: hsl(227, 47%, 96%);
  --dark-grayish-blue-text: hsl(228, 12%, 44%);
  --very-dark-blue-text: hsl(230, 17%, 14%);
}

