*,
*:before,
*:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  background-color: var(--very-dark-blue-background);
  color: var(--white-background);
}

img {
  max-width: 100%;
}

fieldset {
  border: none;
}

body {
  font-family: "Inter", sans-serif;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: clamp(2.4rem, 3vw, 2.8rem);
  white-space: nowrap;
}

h2 {
  font-size: 2.4rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.card--top__top__username {
  font-size: 1.2rem;
  font-weight: 700;
}
.card--top__middle__count {
  font-size: 5.2rem;
  font-weight: 700;
}
@media only screen and (min-width: 62em) {
  .card--top__middle__count {
    font-size: 5.6rem;
  }
}
.card--top__middle__followers {
  font-size: 1.3rem;
  font-weight: 200;
  text-transform: uppercase;
  letter-spacing: 4px;
}
.card__rating {
  font-size: 1.3rem;
  font-weight: 600;
}

.card {
  display: flex;
  max-width: 32.6rem;
  background-color: var(--dark-desaturated-blue-card);
}
.card--top {
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin: 0 0 2.5rem 0;
  padding: 3.2rem 0 2.4rem 0;
}
.card--top__top {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.card--top__top__username {
  margin: 0 0.8rem;
  color: var(--desaturated-blue-card);
}
.card--top__middle {
  padding: 2rem 0 3rem 0;
}
.card--top__middle__followers {
  color: var(--desaturated-blue-card);
}
.card--overview {
  margin: 0 0 1.8rem 0;
}

.cards {
  display: flex;
  flex-direction: column;
}
@media only screen and (min-width: 48em) {
  .cards__top {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}
@media only screen and (min-width: 62em) {
  .cards__top {
    display: flex;
  }
}
@media only screen and (min-width: 48em) {
  .cards__overview__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}
@media only screen and (min-width: 62em) {
  .cards__overview__wrapper {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}

.container {
  width: 100%;
  padding-right: 2.4rem;
  padding-left: 2.4rem;
  margin-right: auto;
  margin-left: auto;
  max-width: 540px;
}
@media only screen and (min-width: 62em) {
  .container {
    width: 100%;
    padding-right: 16.5rem;
    padding-left: 16.5rem;
    margin-right: auto;
    margin-left: auto;
    max-width: 1110px;
  }
}

header {
  margin: 3.8rem 0 2.6rem 0;
}